import express from 'express';
import config from '../config';
import middleware from '../middlewares';
import initializeDb from '../db';
import account from '../controllers/users/account';
import request from '../controllers/transactions/training_request';
import inHouseTraining from '../controllers/transactions/inhouse_training';

let router = express();

// database connetion
initializeDb(db => {

    // internal middleware
    router.use(middleware({ config, db }));

    // API ROUTES v1
    
    // '/v1/account'
    router.use('/account', account({ config, db }));

    // '/v1/request'
    router.use('/request', request({ config, db }));

    // '/v1/ihtraining'
    router.use('/ihtraining', inHouseTraining({ config, db }));
   
});

export default router;